html,body{
   width: 100%;
   height: 100%;
   #root{
    width: 100%;
    height: 100%;
    .app{
        width: 100%;
        height: 100%;
    }
   }
}
.login{
    height: 100%;
    width: 100%;
    background-image: url('../../../static/imgs/bg.jpg');
    header{
       display: flex;
       align-items: center;
        width: 100%;
        height: 80px;
        background-color: rgba(21, 20, 13, 0.5);
        img{
            height: 40px;
            width: 40px;
            margin: 10px;
        }
        h1{
            color:white;
            font-size: 30px;
            margin-bottom: 0px;
        }

    }
    section{
        width: 400px;
        height: 300px;
        background-color: white;
        margin: 0 auto;
        margin-top: 50px;
        padding: 30px;
        h1{
            font-size: 25px;
           text-align: center;
        }
        .login-form-button{
            width: 100%;
        }
        .ant-form-item-control {

            line-height:0px;

        }
    }
}
@primary-color: #1DA57A;